import Vue from 'vue'
import App from './App.vue'

// 
import axios from 'axios'
// 
import router from './routers'
// 
import store from 'store'

Vue.config.productionTip = false

// Vueaxios
Vue.prototype.$axios = axios

// vant
import Vant from './vants/index'
Vant()

new Vue({
    router,
    store,
    render: h => h(App),
}).$mount('#app')