import Vue from 'vue'
// import vant from 'vant'
// 
// 
import {
    //
    Search,
    //
    Tabbar,
    TabbarItem,
    //
    Icon,
    //
    NavBar,
    //
    Field,
    // 
    Button,
    // 
    Cell,
    CellGroup,
    //Tab
    Tab,
    Tabs,
    //
    Swipe,
    SwipeItem,
    // image
    Image,
    // 
    Grid,
    GridItem,
    //
    GoodsAction,
    GoodsActionIcon,
    GoodsActionButton
} from 'vant';


export default () => {
    Vue.use(Tabbar);
    Vue.use(TabbarItem);
    Vue.use(Search);
    Vue.use(Icon);
    //
    Vue.use(NavBar);
    // 
    Vue.use(Field);
    // 
    Vue.use(Button);
    // 
    Vue.use(Cell).use(CellGroup);
    // tab
    Vue.use(Tab).use(Tabs);
    // 
    Vue.use(Swipe).use(SwipeItem);
    // Image 
    Vue.use(Image);
    // 
    Vue.use(Grid).use(GridItem);
    //
    Vue.use(GoodsAction).use(GoodsActionIcon).use(GoodsActionButton);
}